import { Component, OnInit } from '@angular/core';
import { faChevronRight, faUser, faKey, faAddressCard, faStopwatch, faPlus, faEllipsisV, faEdit, faTrash } from '@fortawesome/free-solid-svg-icons';

@Component({
  selector: 'app-profile',
  templateUrl: './profile.component.html',
  styleUrls: ['./profile.component.scss']
})
export class ProfileComponent implements OnInit {

  faChevronRight = faChevronRight;
  faUser = faUser;
  faAddressCard = faAddressCard;
  faKey = faKey;
  faStopwatch = faStopwatch;
  faPlus = faPlus;
  faEllipsisV = faEllipsisV;
  faEdit = faEdit;
  faTrash = faTrash;

  tabs = {  personal: false, address: true, password: false };
  timeLeft = 10;
  timerText: string;
  timeTracker: any;
  OTP: number;
  timerExpired = false;
  timerView = false;

  constructor() { }

  ngOnInit(): void {
  }

  activateTab(name: string){
    console.log('Tab: ' + name);
    switch (name){
      case 'Personal': this.tabs = {  personal: true, address: false, password: false }; break;
      case 'Address': this.tabs = {  personal: false, address: true, password: false }; break;
      case 'Password': this.sendOTP(); this.tabs = {  personal: false, address: false, password: true }; break;
    }
  }

  onOtpChange($event) {
    console.log($event);
    this.OTP = $event;
  }

  countdown() {
    if (this.timeLeft === 0) {
      clearTimeout(this.timeTracker);
      this.timeLeft = 10;
      this.timerText =
        'OTP entered is expired. Please generate a new one and try again';
      this.timerExpired = true;
    } else {
      this.timerView = true;
      this.timerExpired = false;
      this.timerText = 'OTP expires within ' + this.timeLeft + ' seconds';
      this.timeLeft--;
    }
  }

  sendOTP() {
    console.log('Sending OTP');
    clearTimeout(this.timeTracker);
    this.timeLeft = 10;
    this.timerText = 'OTP expires within ' + this.timeLeft + ' seconds';
    this.timerView = false;
    this.timeTracker = setInterval(() => {
      this.countdown();
    }, 1000);
  }
}
